<template>
  <div class="nav-logo">
    <slot></slot>
    <img src="../../assets/logo.png" class="img-logo"/>&nbsp;&nbsp;<span>Hero's flicker</span>
  </div>
</template>

<script>
  import MenuIcon from "../menu-icon";

  export default {
    name: "nav-logo",
    components: {MenuIcon},
    props: {
      hasMenu: {
        default: false,
        type: Boolean
      }
    }
  }
</script>

<style scoped>
  .nav-logo {
    height: 60px;
    line-height: 60px;
    font-size: 20px;
  }
  .img-logo{
    width: 40px;
  }
</style>



