<template>
  <button class="page-ctl" @click="clickHandler" :disabled="disabled" :class="{disabled:disabled,btn:!disabled}">
    <slot></slot>
  </button>
</template>

<script>
  export default {
    name: "page-ctl",
    props: {
      type: {
        type: String,
        default: 'pre'
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      clickHandler() {
        this.$emit('on-click', this.type)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .page-ctl {
    min-width: 30px;
    text-align: center;
    height: 30px;
    line-height: 30px;
    margin: 0 8px;
    display: inline-block;
    transition: all .1s;
    background: none;
    &:hover {
      transform: scale(1.2);
      &.disabled {
        transform: none;
      }
    }
  }
</style>



