<template>
  <div class="tag">
    <div class="text-center inner-content category-list">
      <p>共计<b>{{tags.length}}</b>个标签：</p><br/><br/><br/>
      <div class="tag-box">
        <tag_ class="tag btn" v-for="item in tags" :id="item.id" :text="item.name" :weight="item.weight"
              @on-click="viewDetail"></tag_>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Tag from "../layout/tag";
  import Message from "../layout/message/message";

  export default {
    name: "Tag",
    components: {Message, Tag_: Tag},
    computed: {
      ...mapGetters({
        tags: 'tags'
      })
    },
    data() {
      return {
        showModal: true,
        tag: {
          name: '',
          weight: 1,
        }
      }
    },
    methods: {
      viewDetail(id) {
        this.$router.push(`/jmx/tag/${id}`)
      }
    },
    created() {
      this.$store.dispatch('getTags')
    },
  }
</script>

<style lang="scss" scoped>
  .tag {
    color: #777;
    /*margin-top: 60px;*/
  }

  .tag-box {
    margin: auto 100px;
  }

  .category-list {
    max-width: 750px;
    .tag {
      margin: auto 5px;
      cursor: pointer;
    }
  }
</style>



