module.exports = {
  categories(state, data) {
    state.categories = data
  },
  category(state, data) {
    state.category = data
  },
  tags(state, data) {
    state.tags = data
  },
  tag(state, data) {
    state.tag = data
  },
  articles(state, data) {
    state.articles = data
  },
  article(state, data) {
    state.article = data
  },
  archives(state, data) {
    state.archives = data
  },
  count_article(state, data) {
    state.count.article = data
  },
  count_archive_tag(state, data) {
    state.count.archive.tag = data
  },
  count_archive_category(state, data) {
    state.count.archive.category = data
  },
}



