import createDb from '../apicloud.db'
import config from '../apicloud.db.conf'

const db = createDb(config.appId, config.appKey)
import handleRequest from '../utils'

const className = 'category'
export default {
  async getCategories() {
    return await handleRequest(db.getCategories(className))
  },
  async getCategory(params) {
    return await handleRequest(db.getCategory(className, params))
  },
  async getArticleCountByCategoryId(params) {
    return await handleRequest(db.countRelated(className, params))
  },
}



