<template>
  <div class="article-detail container inner-content">
    <p class="article-title text-center" v-if="!!article">{{article.title}}</p>
    <p class="article-info" v-if="!!article">
      <i class="icon iconfont icon-rili"></i>&nbsp;{{article.createdAt|dateFormat('yyyy-MM-dd')}}&nbsp;&nbsp;|&nbsp;&nbsp;
      <i class="icon iconfont icon-wenjianjia"></i>&nbsp;分类&nbsp;&nbsp;|&nbsp;&nbsp;
      <i class="icon iconfont icon-biaoqian"></i>&nbsp;标签&nbsp;&nbsp;|&nbsp;&nbsp;
      <i class="icon iconfont icon-eye"></i>&nbsp;{{article.view|0}}
    </p>
    <br/><br/><br/>
    <div id="editor" v-if="!!article">
      <mavon-editor class="markdow-editor" style="height: 100%;z-index: 0;"
                    :value="article.content"
                    :boxShadow="false"
                    :subfield="false"
                    defaultOpen="preview"
                    :editable="false"
                    :toolbarsFlag="false"
      ></mavon-editor>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mavonEditor} from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'

  export default {
    name: "ArticleDetail",
    components: {mavonEditor},
    computed: {
      ...mapGetters({
        article: 'article'
      })
    },
    props: {
      id: {
        type: String,
        required: true
      }
    },
    created() {
      this.$store.dispatch('getArticle', this.id)
    }
  }
</script>

<style lang="scss" scoped>
  .article-detail {
    margin-top: 60px;
  }

  .article-title {
    color: #555;
    font-size: 32px;
  }

  .article-info {
    text-align: center;
    margin-top: 12px;
    color: #999;
    font-size: 12px;
  }

  #editor {
    /deep/ .v-show-content {
      background: none;
    }
  }

  .btn-edit {
    color: #0086b3;
  }

  .markdow-editor /deep/ .v-note-panel {
    border: none;
  }
</style>



