<template>
  <div class="nav-bar">
    <transition fade>
      <ul v-if="show" class="ul">
        <router-link to="/jmx/home">
          <li :class="{active:$route.name==='home'}"><i class="icon iconfont icon-home"></i> 首页</li>
        </router-link>
        <router-link to="/jmx/category">
          <li :class="{active:$route.name==='category'}"><i class="icon iconfont icon-categoryselected"></i> 分类</li>
        </router-link>
        <router-link to="/jmx/tag">
          <li :class="{active:$route.name==='tag'}"><i class="icon iconfont icon-tag"></i> 标签</li>
        </router-link>
        <router-link to="/jmx/archive">
          <li :class="{active:$route.name==='archive'}"><i class="icon iconfont icon-archive"></i> 归档</li>
        </router-link>
        <router-link to="/jmx/about">
          <li :class="{active:$route.name==='about'}"><i class="icon iconfont icon-guanyuwo"></i> 关于</li>
        </router-link>
      </ul>
    </transition>
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: "navbar",
    props: {
      show: {
        default: true,
        type: Boolean
      }
    },
  }
</script>

<style lang="scss" scoped>
  .icon {
    font-size: 12px;
    margin-right: 5px;
  }

  .logo {
    height: 60px;
    line-height: 60px;
    margin: auto 20px;
  }

  .nav-bar {
    li {
      display: inline-block;
      height: 60px;
      line-height: 60px;
      padding: 0 10px;
      &:hover {
        background: #0086b3;
        color: #fff;
        &.publish {
          background: none;
          color: #0086b3;
        }
      }
      &.active {
        background: #0086b3;
        color: #fff;
      }
    }
  }

  /*默认样式*/
  .nav-bar.vertical {
    height: 100%;
    li {
      display: block;
    }
  }
</style>



