<template>
  <div class="category container inner-content">
    <p class="text-center">共计<b>{{categories.length}}</b>个分类</p><br/><br/><br/>
    <div class="tag-box">
      <router-link :to="`/jmx/category/${item.id}`" class="tag btn" v-for="item in categories" :id="item.id">
        <label class="tag-label">
          {{item.name}}
        </label>
        &nbsp;<span style="color: #999">({{item.articles.length}})</span>
      </router-link>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Tag from "../layout/tag";

  export default {
    name: "Category",
    components: {Tag},
    computed: {
      ...mapGetters({
        categories: 'categories'
      })
    },
    data() {
      return {
        category: {
          name: '',
          weight: 1,
        }
      }
    },
    created() {
      this.$store.dispatch('getCategories')
    },
  }
</script>

<style lang="scss" scoped>
  .category {
    color: #777;
    .tag-box {
      margin: auto 100px;
    }
    .tag {
      margin: 20px 5px;
      display: block;
      .tag-label {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          bottom: -3px;
          left: 0;
          right: 0;
          height: 0;
          border-bottom: 1px solid #999;
        }
      }
      &:hover {
        .tag-label:after {
          border-bottom-color: #555;
        }
      }
    }
  }
</style>



