import Vue from 'vue'
import Router from 'vue-router'
import JMX from '../views/JMX'
import Home from '../views/Home'
import Category from '../views/Category'
import Archive from '../views/Archive'
import About from '../views/About'
import Tag from '../views/Tag'
import ArticleDetail from '../views/ArticleDetail'
import ArticleTimeLine from '../views/ArticleTimeLine'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      redirect: '/jmx/home',
    },
    {
      path: '/jmx',
      name: 'jmx',
      component: JMX,
      children: [
        {
          path: 'home',
          name: 'home',
          component: Home,
          children: []
        },
        {
          path: 'category',
          name: 'category',
          component: Category
        },
        {
          path: 'archive',
          name: 'archive',
          component: Archive
        },
        {
          path: 'tag',
          name: 'tag',
          component: Tag
        },
        {
          path: 'about',
          name: 'about',
          component: About
        },
        {
          path: 'article/detail/:id',
          props: (route) => ({id: route.params.id}),
          name: 'article-detail',
          component: ArticleDetail
        },
        {
          path: 'tag/:id',
          props: (route) => ({id: route.params.id}),
          name: 'tag',
          component: ArticleTimeLine
        },
        {
          path: 'category/:id',
          props: (route) => ({id: route.params.id}),
          name: 'category',
          component: ArticleTimeLine
        },
      ]
    },
  ]
})



