<template>
  <div class="archive container inner-content">
    <div class="archive-box">
      <div class="archive-year" v-for="(archive,key) in archiveData" :key="key">
        <time-line :title="key" :list="archive" @on-click="viewDetail">
          <span slot="header" class="header">{{key}}</span>
        </time-line>
      </div>
    </div>   <br/><br/><br/><br/><br/>
    <page :page="page" :count="count.article" :each-page-number="page_num" @on-page="onPage" @on-pre="onPre"
          @on-next="onNext"></page>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  import TimeLine from "../layout/time-line";
  import Page from "../layout/page/page";

  export default {
    name: "Archive",
    components: {Page, TimeLine},
    computed: {
      ...mapGetters({
        archives: 'archives',
        count: 'count'
      }),
      archiveData() {
        let data = {}
        this.archives.map(item => {
          let createdAt = item.createdAt
          let year = new Date(createdAt).getFullYear()
          if (!data[year]) data[year] = []
          data[year].push(item)
        })
        return data
      }
    },
    data() {
      return {
        page_num: 10,
        page: 1,
      }
    },
    methods: {
      viewDetail(article) {
        this.$router.push(`/jmx/article/detail/${article.id}`)
      },
      onPage(e) {
        this.getPage(e)
      },
      onPre(e) {
        if (e != this.page) {
          this.getPage(e)
        }
      },
      onNext(e) {
        console.log('next:', this.next)
        if (e != this.page) {
          this.getPage(e)
        }
      },
      getPage(page = 1) {
        let params = {page_num: this.page_num, page: page}
        this.$store.dispatch('getArticlesAsArchive', params).then(res => {
          if (res) {
            this.page = page
          }
        })
      }
    },
    created() {
      this.getPage()
      this.$store.dispatch('getArticleCount')
    }
  }
</script>

<style lang="scss" scoped>
  .archive-box {
    margin: auto 40px;
  }

  .header {
    font-size: 18px;
    color: #a1a1a1;
    font-weight: bold;
    line-height: 32px;
  }
</style>



