<template>
  <div class="time-line">
    <slot name="header">
      <div class="header"><b>{{title}}</b>分类</div>
    </slot>
    <div class="time-line-items" v-for="item in list" :key="item.id||item.index">
      <div class="time-item" @click="clickHandler(item)" v-if="show">
        <span class="date">{{item.createdAt|dateFormat('MM-dd')}}</span>
        <span class="content">{{item.title}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "time-line",
    data() {
      return {
        show: true
      }
    },
    props: {
      list: {
        type: Array,
        required: true
      },
      title: {
        type: String
      }
    },
    methods: {
      clickHandler(item) {
        this.$emit('on-click', item)
      }
    },
  }
</script>

<style lang="scss" scoped>
  .header {
    font-size: 18px;
    color: #a1a1a1;
    font-weight: bold;
    b {
      font-size: 28px;
      margin-right: 5px;
      color: #555;
      font-weight: bolder;
    }
  }

  .time-line {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: -40px;
      top: 0;
      width: 16px;
      height: 16px;
      background: #777;
      border-radius: 8px;
      transform: translate(-6px, 8px);
    }
    &:before {
      content: '';
      position: absolute;
      left: -40px;
      top: 10px;
      bottom: 0;
      width: 4px;
      background: #dedede;
    }
    .time-line-items {
      margin-top: 60px;
      .time-item {
        padding-bottom: 30px;
        position: relative;
        margin-top: 40px;
        .date {
          font-size: 10px;
          padding-right: 12px;
          color: #777;
        }
        .content {
          font-size: 16px;
          color: #555;
        }
        &:active {
          &:after {
            opacity: .5;
          }
        }
        &:hover {
          cursor: pointer;
          &:after {
            border-bottom-color: #555;
          }
        }
        &:after {
          content: '';
          position: absolute;
          right: 0;
          left: -36px;
          border-bottom: 2px dashed #dedede;
          bottom: 10px;
        }
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: -40px;
          width: 8px;
          height: 8px;
          background: #afafaf;
          border-radius: 8px;
          transform: translate(-2px, 4px);
        }
        &:hover {
          &:before {
            background: #555;
          }
        }
      }
    }
  }
</style>



