<template>
  <div class="home container inner-content">
    <div class="article-panel" v-for="article in articles" :key="article.id">
      <p class="article-title">{{article.title}}</p>
      <p class="article-info">
        <i class="icon iconfont icon-rili"></i>&nbsp;{{article.createdAt|dateFormat('yyyy-MM-dd')}}&nbsp;&nbsp;|&nbsp;&nbsp;
        <i class="icon iconfont icon-wenjianjia"></i>&nbsp;{{article.category.name}}&nbsp;&nbsp;|&nbsp;&nbsp;
        <i class="icon iconfont icon-biaoqian"></i>&nbsp;{{article.tag.name}}&nbsp;&nbsp;|&nbsp;&nbsp;
        <i class="icon iconfont icon-eye"></i>&nbsp;{{article.view|0}}
      </p>
      <p class="article-content">
        {{article.content}}
      </p>
      <div class="btn btn-see-all" @click="viewArticle(article.id)">阅读全文<span>>>></span></div>
    </div>
    <br/><br/><br/><br/><br/>
    <page :page="page" :count="count.article" :each-page-number="page_num" @on-page="onPage" @on-pre="onPre"
          @on-next="onNext"></page>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Page from "../layout/page/page";

  export default {
    name: "Home",
    components: {Page},
    computed: {
      ...mapGetters({
        articles: 'articles',
        count: 'count'
      })
    },
    data() {
      return {
        page_num: 10,
        page: 1,
      }
    },
    methods: {
      viewArticle(id) {
        this.$store.dispatch('articleView', id).then(res => {
          if (res) {
            this.$router.push('/jmx/article/detail/' + id)
          }
        })
      },
      onPage(e) {
        this.getPage(e)
      },
      onPre(e) {
        if (e != this.page) {
          this.getPage(e)
        }
      },
      onNext(e) {
        console.log('next:', this.next)
        if (e != this.page) {
          this.getPage(e)
        }
      },
      getPage(page = 1) {
        this.$store.dispatch('getArticles', {page_num: this.page_num, page: page}).then(res => {
          if (res) {
            this.page = page
          }
        })
      }
    },
    created() {
      // this.$store.dispatch('getArticles', {page_num: this.page_num, page: this.page})
      this.getPage()
      this.$store.dispatch('getArticleCount')
    }
  }
</script>

<style lang="scss" scoped>
  .home {
    margin-top: 60px;
  }

  .article-title {
    color: #555;
    font-size: 28px;
  }

  .article-info {
    display: flex;
    align-items: center;
    margin-top: 10px;
    color: #999;
    font-size: 12px;
  }

  .article-content {
    color: #777;
    margin-top: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    line-height: 180%;
  }

  .article-panel {
    margin-top: 100px;
    &:first-child {
      margin-top: 0;
    }
  }

  .btn-see-all {
    color: #666;
    margin-top: 24px;
    font-size: 14px;
    color: #0086b3;
    display: inline-block;
    border-bottom: 1px solid #0086b3;
    padding-bottom: 5px;
    span {
      font-size: 10px;
      vertical-align: middle;
    }
  }

  .btn-cancel {
    color: #a71d5d;
    font-size: 14px;
  }
</style>



