import axios from 'axios'
import sha1 from 'sha1'
import Vue from 'vue'
import filters from "../filters/filters";

const baseUrl = 'https://www.apicloud.com/mcm/api'
const request = axios.create({
  baseURL: baseUrl,
})

const createError = (code, message) => {
  // console.log('-------------------------------', message)
  const err = new Error(message)
  err.code = code
  return err
}
const successResponse = data => {
  return {
    data: data,
    success: true
  }
}
const handleRequest = ({status, data, ...resp}) => {
  // console.log(status, data, resp)
  if (status === 200) {
    let {statusCode, message} = data
    if (statusCode) {
      throw createError(statusCode, message)
    }
    return successResponse(data)
  }
  // if (!data) {
  //   throw createError(400, 'no data')
  // }
  // if (!data.success) {
  //   throw createError(400, 'data.message')
  // }
  /*error: {name: "Error", status: 401, message: "Access Denied", statusCode: 401}
message: "Access Denied"
name: "Error"
status: 401
statusCode: 401*/
  throw createError(status, resp.message)
}

export default (appId, appKey) => {
  const getHeaders = () => {
    const Now = Date.now()
    return {
      'X-APICloud-AppId': appId,
      'X-APICloud-AppKey': `${sha1(`${appId}UZ${appKey}UZ${Now}`)}.${Now}`
    }
  }
  return {
    async count(className) {
      return handleRequest(await
        request.get(`/${className}/count`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    /*统计关联数量*/
    async countRelated(className, params) {
      let {id, relationColName} = params
      return handleRequest(await
        request.get(`/${className}/${id}/${relationColName}/count`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    /*--------------------------------分类-------------------------------*/
    async getCategories(className) {
      let filters = {
        include: ['articles'],
        includefilter: {
          article: {
            fields: ['id'],
          }
        }
      }
      return handleRequest(await
        request.get(`/${className}?filter=${JSON.stringify(filters)}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },

    async getCategory(className, params) {
      let {id, page_num, page} = params
      let filters = {
        where: {
          id: id
        },
        fields: ['id', 'name'],
        include: ['articles'],
        includefilter: {
          article: {
            fields: ['createdAt', 'id', 'title'],
            limit: page_num,
            skip: (page - 1) * page_num,
          }
        }
      }
      return handleRequest(await
        request.get(`/${className}/?filter=${JSON.stringify(filters)}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    /*--------------------------------标签-------------------------------*/
    async getTags(className) {
      return handleRequest(await
        request.get(`/${className}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async getTag(className, params) {
      let {id, page_num, page} = params
      let filters = {
        where: {
          id: id
        },
        fields: ['id', 'name'],
        include: ['articles'],
        includefilter: {
          article: {
            fields: ['createdAt', 'id', 'title'],
            limit: page_num,
            skip: (page - 1) * page_num,
          },
        },
      }
      return handleRequest(await
        request.get(`/${className}/?filter=${JSON.stringify(filters)}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    /*-------------------------------文章管理-------------------------------*/
    async getArticles(className, params) {
      let {page_num, page} = params
      // console.log('pa-------------', params)
      let fiter = {
        limit: page_num,
        skip: (page - 1) * page_num
      }
      return handleRequest(await
        request.get(`/${className}?filter=${JSON.stringify(fiter)}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async getArticlesAsArchive(className, params) {
      let {page_num, page} = params
      let filters = {
        fields: ['title', 'id', 'createdAt'],
        order: "audibleRange ASC",
        limit: page_num,
        skip: (page - 1) * page_num
      }
      return handleRequest(await
        request.get(`/${className}?filter=${JSON.stringify(filters)}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    async getArticle(className, id) {
      return handleRequest(await
        request.get(`/${className}/${id}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    async viewArticle(className, id) {
      let inc = {
        $inc: {"view": +1}
      }
      return handleRequest(await
        request.put(`/${className}/${id}`, inc, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
  }
}




