import CATA_API from '../../model/apis/category.requests'
import TAG_API from '../../model/apis/tag.requests'
import ARTICLE_API from '../../model/apis/article.requests'

export default {
  /*-----------------------分类-----------------------------------*/
  async getCategories({commit}) {
    let data = await CATA_API.getCategories()
    commit('categories', data)
  },
  async getCategory({commit}, params) {
    let data = await CATA_API.getCategory(params)
    commit('category', data.length === 1 ? data[0] : null)
  },
  /*-----------------------------标签-----------------------------*/
  async getTags({commit}) {
    // TAG_API.getTags().then(data => {
    //   commit('tags', data)
    // })
    let data = await TAG_API.getTags()
    commit('tags', data)
    // return Promise.resolve(data) //若组件内需要获取到
  },
  async getTag({commit}, params) {
    let data = await TAG_API.getTag(params)
    commit('tag', data.length === 1 ? data[0] : null)
  },
  /*-----------------------------文章-----------------------------*/
  //获取文章总数
  async getArticleCount({commit}) {
    let data = await ARTICLE_API.getArticleCount()
    if (data)
      commit('count_article', data.count)
  },
  async getArticles({commit}, params) {
    let data = await ARTICLE_API.getArticles(params)
    commit('articles', data)
    return 'ok'
    // return Promise.resolve(data) //若组件内需要获取到
  },
  /*获取文章用于归档*/
  async getArticlesAsArchive({commit}, params) {
    console.log('pppp', params)
    let data = await ARTICLE_API.getArticlesAsArchive(params)
    commit('archives', data)
    return 'ok'
  },
  async getArticle({commit}, id) {
    let data = await ARTICLE_API.getArticle(id)
    commit('article', data)
    return data
  },
  async articleView({commit}, id) {
    return await ARTICLE_API.articleView(id)
  },
  /*根据tag id查询总数*/
  async getArticleCountByTagId({commit}, id) {
    let data = await TAG_API.getArticleCountByTagId(id)
    if (data.count)
      commit('count_archive_tag', data.count)
  },
  /*根据tag id查询总数*/
  async getArticleCountByCategoryId({commit}, id) {
    let data = await CATA_API.getArticleCountByCategoryId(id)
    if (data.count)
      commit('count_archive_category', data.count)
  },
}



