<template>
  <div class="article-time-line inner-content container">
    <div class="timeline-wrap" v-if="category&&$route.name === 'category'">
      <time-line :list="category.articles" :title="category.name" @on-click="viewDetail"></time-line>
    </div>
    <div class="timeline-wrap" v-if="tag&&$route.name === 'tag'">
      <time-line :list="tag.articles" :title="tag.name" @on-click="viewDetail"></time-line>
    </div>
    <br/><br/><br/><br/><br/>
    <page :page="page" :count="count_" :each-page-number="page_num" @on-page="onPage" @on-pre="onPre"
          @on-next="onNext"></page>
  </div>
</template>

<script>
  import TimeLine from "../layout/time-line";
  import {mapGetters} from 'vuex'
  import Page from "../layout/page/page";

  export default {
    name: "ArticleTimeLine",
    components: {Page, TimeLine},
    props: {
      id: {
        type: String
      }
    },
    computed: {
      ...mapGetters({
        category: 'category',
        tag: 'tag',
        count: 'count'
      }),
      count_() {
        return this.$route.name === 'category' ? this.count.archive.category : this.count.archive.tag
      }
    },
    data() {
      return {
        page_num: 10,
        page: 1,
      }
    },
    methods: {
      viewDetail(article) {
        this.$router.push(`/jmx/article/detail/${article.id}`)
      },
      onPage(e) {
        this.getPage(e)
      },
      onPre(e) {
        if (e != this.page) {
          this.getPage(e)
        }
      },
      onNext(e) {
        console.log('next:', this.next)
        if (e != this.page) {
          this.getPage(e)
        }
      },
      getPage(page = 1) {
        let name = this.$route.name === 'category' ? 'getCategory' : 'getTag'
        this.$store.dispatch(name, {id: this.id, page_num: this.page_num, page: page}).then(res => {
          if (res) {
            this.page = page
          }
        })
      }
    },
    created() {
      this.getPage()
      if (this.$route.name === 'category') {
        this.$store.dispatch('getArticleCountByCategoryId', {id: this.id, relationColName: 'articles'})
      } else {
        this.$store.dispatch('getArticleCountByTagId', {id: this.id, relationColName: 'articles'})
      }
    }
  }
</script>

<style scoped>
  .timeline-wrap {
    margin: auto 100px;
  }

</style>



