<template>
  <div class="page"><br/>
    <page-ctl role="pre" @on-click="pageChangeHandler('pre')" :disabled="this.page===1"><i
      class="icon iconfont icon-arrowdown icon-pre"></i>
    </page-ctl>
    <page-bar v-for="number in pages" :text="number" :active="page===number" @on-page="pageChange"></page-bar>
    <page-ctl role="next" @on-click="pageChangeHandler('next')" :disabled="this.page===this.page_num"><i
      class="icon iconfont icon-arrowdown icon-next"></i></page-ctl>
    &nbsp;&nbsp;共<b>{{this.count}}</b>条 / 每页<b>{{this.eachPageNumber}}</b>条记录
  </div>
</template>

<script>
  import PageBar from "./page-bar";
  import PageCtl from "./page-ctl";

  export default {
    name: "page",
    components: {PageCtl, PageBar},
    props: {
      page: {
        default: 1,
        type: Number
      },
      count: {
        default: 0,
        type: Number
      },
      eachPageNumber: {
        default: 10,
        type: Number
      },
    },
    computed: {
      page_num() {
        return Math.ceil(this.count / this.eachPageNumber)
      },
      pre() {
        return this.page > 1 ? this.page - 1 : 1
      },
      next() {
        return this.page < this.page_num ? this.page + 1 : this.page_num
      },
      pages() {
        let page_num = this.page_num
        let pre = this.pre
        let next = this.next
        let numbers = []
        if (pre >= next) return [1]
        if (pre < next) {
          for (let k = pre; k <= next; k++) {
            numbers.push(k)
          }
        }
        if (pre > 2) {
          numbers.unshift('...')
          numbers.unshift(1)
        } else if (pre === 2) {
          numbers.unshift(1)
        }
        if (next < page_num - 1) {
          numbers.push('...')
          numbers.push(page_num)
        } else if (next === page_num - 1) {
          numbers.push(page_num)
        }
        return numbers
      }
    },
    methods: {
      pageChange(e) {
        this.$emit('on-page', e)
      },
      pageChangeHandler(text) {
        console.log(text)
        if (text === 'pre') {
          this.$emit('on-pre', this.pre)
        } else {
          this.$emit('on-next', this.next)
        }
      }
    }
  }
</script>

<style scoped>
  .page {
    border-top: 1px solid #efefef;
    color: #777;
  }

  .icon-pre {
    display: inline-block;
    transform: rotate(90deg);
    color: #555;
  }

  .icon-next {
    display: inline-block;
    transform: rotate(-90deg);
  }
</style>



