export default {
  categories: state => {
    return state.categories
  },
  category: state => {
    return state.category
  },
  tags: state => {
    return state.tags
  },
  tag: state => {
    return state.tag
  },
  articles: state => {
    return state.articles
  },
  article: state => {
    return state.article
  },
  archives: state => {
    return state.archives
  },
  count: state => {
    return state.count
  },
}



