<template>
  <label class="page-bar btn" :class="{active:!!active,disabled:(text=='...')}"
         @click="clickHandler">{{text}}</label>
</template>

<script>
  export default {
    name: "page-bar",
    props: {
      text: {
        type: String | Number,
        default: ''
      },
      active: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      clickHandler() {
        if (this.text !== '...') {
          this.$emit('on-page', this.text)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .page-bar {
    color: #777;
    margin: 0 8px;
    position: relative;
    font-size: 16px;
    display: inline-block;
    min-width: 30px;
    text-align: center;
    height: 30px;
    line-height: 30px;
    border-radius: 15px;
    &.active {
      background: #afafaf;
      color: #ffffff;
    }
    &:before {
      position: absolute;
      content: '';
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      border-radius: 50%;
      border: 1px solid #777;
      opacity: 0;
      transition: all .5s;
    }
    &:hover {
      &:before {
        opacity: 1;
      }
    }
    &.disabled {
      &:hover {
        &:before {
          opacity: 0;
        }
      }
    }
  }
</style>



